/* eslint-disable */

var CACHE = 'beer-cache-v1'
var BEER_CACHE = 'beers'

self.addEventListener('install', event => {
    console.log('The service worker is being installed.')
    event.waitUntil(precache())

    if (Notification.permission === 'granted') {
        event.waitUntil(
            self.registration.showNotification('Loaded', {
                "body": "Install complete",
                "tag": "install-completed"
            })
        )
    }
})

self.addEventListener('fetch', function(event) {
    if (event.request.url.startsWith('https://images.punkapi.com')) {
        event.respondWith(update(event))
        return
    }

    if (event.request.url.startsWith('https://api.punkapi.com')) {
        event.respondWith(update(event))
        return
        // TODO save to indexDB
    }

    event.respondWith(
        caches.match(event.request)
            .then(response => response || fetch(event.request))
    )
})

function precache() {
    return caches.open(CACHE)
        .then(cache => cache.addAll([
            ...serviceWorkerOption.assets,
            '/icon.png',
            '/home',
            '/favorites',
            '/join'
        ]))
}

function fromCache(request) {
    return caches.open(CACHE).then(function (cache) {
        return cache.match(request).then(function (matching) {
            return matching || Promise.reject('no-match')
        })
    })
}

function update(event) {
    const networkFetch = fetch(event.request)

    event.waitUntil(
        networkFetch.then(response => {
            const responseClone = response.clone()
            caches.open(BEER_CACHE).then(cache => {
                cache.put(event.request, responseClone)
            })
        })
    )

    return caches.match(event.request)
        .then(response => response || networkFetch)
}
